.Application {
  font-family: sans-serif;
  text-align: center;
}

#root {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.viewer-wrapper {
  width: 100%;
}

.channelBtn {
  font-size: 12px;
  max-width: 60%;
}
.viewBtn {
  font-size: 10px;
  max-width: 40%;
}
.annotChnlVeiw {
  max-width: 95%;
}
.slider {
  -webkit-appearance: none;
  width: 70%;
  height: 10px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 10px;
  height: 10px;
  background: #4CAF50;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 10px;
  height: 10px;
  background: #4CAF50;
  cursor: pointer;
}
